* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Jost', sans-serif;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
.body {
  margin: 0;
  padding: 0;
  font-family: 'Jost', sans-serif;
  min-height: 100vh;
  display: grid;
  grid-template-columns: min-content 1fr;
  grid-template-rows: min-content 1fr min-content;
  grid-template-areas: "nav header" "nav main" "nav footer";
  background-color: var(--back-color);
}
.body--scrolloff {
  overflow: hidden;
}
.neumorphic-button {
  background-color: transparent;
  border: none;
  box-shadow: var(--shadow);
  border-radius: 15px;
  cursor: pointer;
  transition: 0.3s ease;
}
.neumorphic-button:active {
  box-shadow: var(--inner-shadow);
}
.neumorphic-search {
  border-radius: 15px;
  border: none;
  box-shadow: var(--inner-shadow);
  background: none;
}
.neumorphic-search:focus {
  outline: none;
  box-shadow: var(--shadow);
}
.theme-switcher {
  position: relative;
  margin: 0;
  margin-left: -50px;
  padding: 5px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  border: none;
}
.theme-switcher:hover {
  box-shadow: var(--inner-shadow);
}
/* Switcher Radio */
.theme-switcher__radio {
  cursor: pointer;
  -webkit-appearance: none;
  appearance: none;
  margin: 0;
  width: 32px;
  height: 32px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 24px;
  transition: filter 0.1s ease-in;
}
.theme-switcher__radio:focus {
  outline: none;
}
.theme-switcher__radio--light {
  background-image: url('../img/icons/light.svg');
}
.theme-switcher__radio--auto {
  background-image: url('../img/icons/auto.svg');
}
.theme-switcher__radio--dark {
  background-image: url('../img/icons/dark.svg');
}
/* Switcher Status */
.theme-switcher__status {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  background-repeat: no-repeat;
  background-image: url('../img/icons/status.svg');
  background-size: 30px;
  background-position: center;
  transition: background-position 0.1s ease-in;
}
.theme-switcher__radio:focus-visible ~ .theme-switcher__status {
  box-shadow: 0 0 0 2px black;
}
.theme-switcher__radio--light:checked ~ .theme-switcher__status {
  background-position: left 6px center;
}
.theme-switcher__radio--auto:checked ~ .theme-switcher__status {
  background-position: center center;
}
.theme-switcher__radio--dark:checked ~ .theme-switcher__status {
  background-position: right 6px center;
}
.header {
  width: 100%;
  padding: 20px;
  grid-area: header;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 900px) {
  .header--open-menu .header__menu-btn {
    position: absolute;
    top: 20px;
    left: 20px;
    z-index: 2;
    transition: all 1s ease-in-out;
    animation-duration: 1s;
    animation-name: show-mobile-btn;
  }
}
.header--open-menu .header__welcome {
  width: 0px;
  overflow: hidden;
}
.header--open-menu .header__search-input {
  width: 100%;
}
.header__wrap {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: auto auto 1fr min-content;
  grid-column-gap: 20px;
  align-items: center;
}
@media (max-width: 900px) {
  .header__wrap {
    grid-template-rows: repeat(3, 1fr);
    grid-template-columns: 1fr min-content;
    grid-template-areas: "btn controls" "hello controls" "search search";
    grid-gap: 10px;
  }
}
