}
@keyframes show-mobile-btn {
  0% {
    opacity: 0;
  }
  80% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.navigation {
  width: auto;
  max-height: 100vh;
  grid-area: nav;
  overflow-y: auto;
  position: relative;
  z-index: 1;
  -ms-overflow-style: none;
  /* IE 11 */
  scrollbar-width: none;
  /* Firefox 64 */
}
.navigation::-webkit-scrollbar {
  width: 0;
}
.navigation::-moz-scrollbar {
  width: 0px;
}
@media (max-width: 900px) {
  .navigation {
    position: absolute;
    top: 0;
    left: 0;
  }
}
.open > .navigation__Wrap {
  width: 300px;
  transform: translateX(0%);
  transition: transform 1s ease, width 1s ease;
}
@media (max-width: 900px) {
  .open > .navigation__Wrap {
    width: 350px;
  }
}
@media (max-width: 900px) {
  .open {
    width: 100%;
    background-color: var(--menu-mobile-bg-color);
  }
}
.navigation__Wrap {
  width: 0px;
  transform: translateX(-100%);
  overflow-x: hidden;
  transition: transform 1s ease, width 1s ease;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: flex-start;
  gap: 20px;
}
@media (max-width: 900px) {
  .navigation__Wrap {
    background-color: var(--menu-mobile-bg-color);
  }
}
.navigation__logo {
  align-self: center;
  padding: 10px 0 0 0;
  min-width: 300px;
  height: min-content;
  object-fit: contain;
}
@media (max-width: 900px) {
  .navigation__logo {
    width: 150px;
    height: 100px;
    align-self: flex-end;
  }
}
.navigation__list {
  min-width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: start;
  gap: 10px;
  padding-left: 10px;
  padding-bottom: 20px;
  list-style: none;
}
@media (max-width: 900px) {
  .navigation__list {
    min-width: 350px;
    padding-left: 70px;
  }
}
.navigation__title {
  font-family: 'Open Sans', sans-serif;
  font-size: 18px;
  font-weight: 600;
  color: var(--sub-text-color);
}
.navigation__item {
  position: relative;
}
.navigation__item::before {
  content: '';
  position: absolute;
  top: -5px;
  left: -5px;
  width: calc(100% + 10px);
  height: calc(100% + 10px);
  background-color: transparent;
  border-radius: 8px;
}
.navigation__item:hover::before {
  background-color: var(--menu-hover-color);
}
.navigation__link {
  height: 100%;
  width: 100%;
  font-family: 'Jost', sans-serif;
  font-weight: 400;
  color: var(--standart-text-color);
  font-size: 22px;
  text-decoration: none;
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 10px;
  position: relative;
  padding-left: 40px;
}
.navigation__link:hover {
  color: var(--hover-text-color);
}
.navigation__link::after {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 35px;
  height: 35px;
  border-radius: 5px;
  box-shadow: var(--shadow);
  background-repeat: no-repeat;
  background-size: 80%;
  background-position: center;
}
